<template>
    <div class="hash">
        <div class="hello">
            <h1>多页面路由模式</h1>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'hash',
    data () {
      return {
        msg: '多页面路由模式'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



